.slider {
  position: relative;
  max-width: 100%;
  a {
    outline: none;
  }
}

.slick-arrow {
  position: relative;
  padding: 10px 0;
  margin: 0;
  border: none;
  text-align: center;
  transition: 0.3s; // position: absolute;
  background: rgba(#000, 0.5);
  overflow: hidden;
  text-indent: -999px; // top: 50%;
  // transform: translateY(-50%);
  z-index: 10;
  border: 1px solid rgba(#fff, 0.15);
  transition: 0.2s;
  &:hover {
    border-color: $warning;
  }
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  @include size(36px, 55px);
  @include min(1025) {
    &:hover {
      color: $warning;
    }
  }
  &.slick-disabled {
    pointer-events: none;
    opacity: 0.5;
  }
  &:focus {
    outline: none;
  }
}

.slick-prev {
  left: 0;
  &::after {
    @extend .slide-prev;
  }
}

.slick-next {
  right: 0;
  &::after {
    @extend .slide-next;
  }
}

@keyframes paginationLine {
  0% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}

.slick-dots {
  @extend .list-unstyled;
  text-align: center;
  font-size: 0;
  padding: 5px 10px;
  button {
    // padding: 0;
    // @include size(8px);
    // border-radius: 100%;
    // background-color: $font-color;
    // border: none;
    // transition: 0.2s;
    // &:hover{
    //     background-color: $accent;
    // }
    outline: none;
    border: none;
    background: #626262;
    position: relative;
    width: 40px;
    height: 5px;
    @include min(480) {
      @include size(70px, 5px);
    }
    @include min(767) {
      @include size(90px, 5px);
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      @include size(100%);
      transform-origin: 0 0;
      background: #ff8300;
      transform: scaleX(0);
      animation-timing-function: linear;
      animation-fill-mode: forwards;
      animation-duration: 5s;
    }
    &:hover {
      background: #b1b1b1;
    }
  }
  li {
    display: inline-block;
    vertical-align: top;
    padding: 10px 6px;
    @include min(767) {
      padding: 12px;
    }
    &.slick-active {
      button {
        background-color: $accent;
        &::after {
          // transition: 5s linear;
          // transform: scaleX(1);
          .slider:hover &,
          .slick-slider:hover & {
            animation: none;
          }
          animation-name: paginationLine;
        }
      }
    }
  }
}

.slider-controls {
  // display: flex;
  // position: absolute;
  bottom: 7px;
  left: 0;
  width: 100%;
  padding: 0 15px; // width: calc(100% + 30px);
  // left: -15px;
  // top: 50%;
  .slick-arrow {
    pointer-events: auto;
  }
  text-align: center;
  &__inner {
    display: inline-block;
    position: relative;
    margin-bottom: 20px;

    @include min(600) {
      padding: 0 70px;
    }

    .slick-arrow {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }

    .slick-prev {
      left: 0;
    }
    .slick-next {
      right: 0;
    }
  }
}

.main-slider {
  position: relative;
  z-index: 5 !important;

  .slogan {
    font-size: 20px;
    line-height: (30/35);
    font-weight: 300;
    margin: em(20, 35) 0 em(27, 35);
    @include min(767) {
      font-size: 30px;
    }

    @include min(992) {
      font-size: 35px;
    }
  }
  .btn-slider-sm {
    padding: 0 18px;
    min-height: 40px;
    padding-bottom: 2px;
    .btn-icon {
      color: $warning;
    }
  }

  // &::after{
  //     content: '';
  //     position: absolute;
  //     left: 0;
  //     right: 0;
  //     bottom: 0;
  //     border-top: 1px solid rgba(#fff, 0.15);
  //     pointer-events: none;
  //     z-index: 10;
  // }
  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin-top: 40px;
    @include min(1300) {
      transform: translateY(-50%);
      margin-top: 25px;
    }
  }
  .slick-next {
    right: 10px;
    @include min(992) {
      right: 5vw;
    }
  }
  .slick-prev {
    left: 10px;
    @include min(992) {
      left: 5vw;
    }
  }
  .slick-dots {
    position: absolute;
    left: 0;
    width: 100%;
    bottom: 0;
  }
}

