.main-slide {
  $break: $slider-break;
  display: block !important;
  padding-top: 83px;
  height: 545px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  @include min(1300) {
    padding-top: 53px;
  }
  @include min($break) {
    height: 365px;
    background-position: bottom right;
  }
  &::after {
    border-bottom: 1px solid rgba(#fff, 0.15);
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
  &--1 {
    background-image: url(../img/pictures/slide-bg-1-1.jpg);
    @include min($break) {
      background-image: url(../img/pictures/slide-bg-1.jpg);
    }
    .main-slide__container {
      flex-direction: row-reverse;
    }
  }
  &--2 {
    background-image: url(../img/pictures/slide-bg-2-2.jpg);
    @include min($break) {
      background-image: url(../img/pictures/slide-bg-2.jpg);
    }
  }
  &--3 {
    background-image: url(../img/pictures/slide-bg-3-3.jpg);

    @include min($break) {
      background-image: url(../img/pictures/slide-bg-3.jpg);
    }
  }
  &--4 {
    background-image: url(../img/pictures/slide-bg-4-4.jpg);
    @include min($break) {
      background-image: url(../img/pictures/slide-bg-4.jpg);
    }
  }
  &--2,
  &--3,
  &--4 {
    @include max($break - 1) {
      .main-slide__container {
        display: flex;

        .col {
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
        }
      }
    }
  }
  &__container {
    max-width: 960px;
    margin: 0 auto;
    height: 100%;
    padding: 10px 37px 40px;
    @include min($break) {
      display: flex;
      padding: 10px 45px 40px;
      align-items: center;
    }
    @include min(1200) {
      padding-left: 0;
      padding-right: 0;
    }
  }
  &__logo-1 {
    text-align: center;
    img {
      display: inline-block; // animation-timing-function: linear;
      // animation-fill-mode: forwards;
      // animation-duration: 15s;
      @include min(330) {
        max-width: 330px;
        margin-bottom: 45px;
      }
      @include min($break) {
        margin-bottom: 0;
        max-width: 400px; // .main-slider:hover &{
        //     animation-play-state: paused;
        // }
      }
      @include min(992) {
        max-width: 475px;
      }
      @include min(1100) {
        transition: 5s;
        transform: translate(20px, 0);
        .slick-current & {
          transition: 15s;
          transform: translate(0, 0);
        }
      }
    }
  }
  &__text {
    font-size: 19px;
    line-height: 30px;
    color: #fff;
    padding: 0 10px;
    @include max($break - 1) {
      text-align: center;
    }
    @include min($break) {
      font-size: 22px;
      line-height: 25px;
    }
    .ttl-large {
      margin-bottom: 7px;
      text-transform: uppercase;
      line-height: (40/35);
      @include max($break - 1) {
        font-size: 35px;
      }
    }
  }
  &__order {
    padding-top: 20px;
    .btn {
      @include max($break - 1) {
        width: 100%;
      }
      max-width: 310px;
      // &:hover {
      //     color: #fff;
      // }
    }
  }
}

.index-slide {
  min-height: 460px;
  position: relative;
  background-position: top left;

  @include min(575) {
    max-height: 680px;
    height: 100vh;
  }
  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid rgba(#fff, 0.15);
  }
  &__container {
    opacity: 0;
    transition: 0.8s;
    transform: translateX(-15px);

    .slick-active & {
      transform: none;
      opacity: 1;
    }
  }

  &__logo {
    img {
      display: inline-block !important;
    }
  }
}

.index-slider {
  .slick-next {
    @include min(992) {
      right: 10vw;
    }
  }
  .slick-prev {
    @include min(992) {
      left: 10vw;
    }
  }
}
