.page-nav {
    position: fixed;
    left: 1vw;
    top: 50%;
    transform: translateY(-50%);
    z-index: 100;
    pointer-events: none;
    width: 100%;

    @include max(1150){
        display: none;
    }

    &__list {
        display: inline-block;
        pointer-events: auto;
    }
    &__item {
        &:not(:last-child){
            margin-bottom: 13px;
        }
    }
    &__link {        
        font-size: 14px;
        font-weight: 600;
        display: flex;
        align-items: center;
        line-height: 1;
        min-height: 20px;
        padding-left: 4px;
        border-left: 2px solid currentColor;
        color: #d2d2d2;

        // .switchColor & {
        //     color: #fff;
        // }

        &--kids{
            &:not(:hover){
                color: #71c56e;
            }
        }
        &--video{
            transition: none;
            &:hover{
                .icon{
                    @extend .icon-play-h;
                }
            }
        }
        
        &:hover,
        &.isCurrent{
            color: $accent;
            font-size: 16px;
        }

        *{
            pointer-events: none;
        }
    }
    &__icon{
        
    }
}