
.faq {
    padding-top: 0;
    // padding-bottom: 75px;
    // @include min(1100) {
    //     padding-bottom: 150px;
    // }
    .ttl-large {
        margin-bottom: em(20, 45);
    }

    &__contacts{
        background: #151a1e url(../img/images/faq-contacts-bg.jpg) no-repeat left top / cover;
        color: #fff;
    }
}

.faq-list {
    max-width: 960px;
    margin: 0 auto;
}

.faq-tabs-box,
.faq-tabs{
    position: relative;
    
}
.faq-tabs{
    margin-bottom: 40px;
}

.faq-box {
   
    $s: &;
    &:not(:last-child) {
        margin-bottom: 30px;
    }
    &__row {
        display: flex;
        .lg & {
            @include max(766) {
                display: block;
            }
        }
        @include max(575) {
            display: block;
        }
        +#{$s}__content {
            @include min(992) {
                padding-left: 20px;
            }
        }
    }
    &__thumb {
        position: relative;
        margin: 0 35px 20px 0;
        flex-shrink: 0;
        max-width: 185px;
        
    align-self: flex-start;
        .lg & {
            max-width: 270px;
        }
        img {
            display: block;
        }
    }
    &__ttl {
        font-size: 19px;
        font-weight: 600;
        .lg & {
            font-size: 22px;
        }
    }
    &__caption {
        color: #fff;
        font-size: 17px;
        position: absolute;
        left: 28px;
        top: 0.35em;
        .lg & {
            font-size: 19px;
            font-weight: 800;
        }
    }
    &__content {
        font-size: 19px;
        line-height: (23/19);
    }
}


.faq-header{
    background: #323237 url(../img/images/faq-top-bg.jpg) no-repeat top left / cover;
    color: #fff;
    padding-top: 100px;
    padding-bottom:60px;

    .ttl-md{
        font-size: 30px;
    }

    p{
        font-weight: 300;
        font-size: 25px;
        line-height: 1;
    }
}

.faq-nav-box{
    overflow: hidden;
    background: #131516 url(../img/images/faq-bg.jpg) no-repeat top center / cover;
    color: #fff;
    background-attachment: fixed;
    padding-bottom: 55px;

    .order-btn{
        white-space: normal;
    }
}

.faq-nav-btn {
    // border: none;
    // background: #fff;
    @extend .slick-arrow;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    // padding:  7px 5px;
    // border-radius: 2px;
    // outline: none;
    // z-index: 100;
    // outline: none;

    
    // &:focus{
    //     outline: none;
    // }
    // &:hover {
    //     color: $warning;
    //     transition: 0.2s;
    // }
    &--prev {
        left: 0;
        // @include max(576){
        //     left: -15px;
        // }
        @extend .slick-prev;
    }
    &--next {
        @extend .slick-next;
        right: 0;
        // @include max(576){
        //     right: -15px;
        // }
    }
}
.faq-nav-scroller{
    overflow: auto;
}
.faq-nav-wrp{
    position: relative;
    // height: 164px;

    &::after{
        content: '';
        position: absolute;
        border-bottom: 1px solid rgba(#fff, 0.15);
        bottom: 12px;
        pointer-events: none;
        left: 0;    
        width: 100%;
        z-index: 10;
    }
}
.faq-nav {
    // padding: 24px 0;
    
    position: relative;
    
    &__list {
        font-size: 0;
        padding: 18px 0 30px !important;
        // margin: 10px 0 20px;
        white-space: nowrap;
    }
    &__item {
        padding: 7px;
        flex-shrink: 0;
        // float: left;
        display: inline-block;
    }
    &__link {
        position: relative;
        display: inline-block;
        min-width: 115px;
        color: #fff;
        line-height: 22px;
        height: 120px;
        background-size: cover;
        background-repeat: no-repeat;
        .faq-nav__item:not(:first-child) &{
            width: 270px;
        }
        transition: color 0.2s, transform 0.1s;
        &:hover{
            color: $warning;
            transform: scale(1.01);
        }
    }
    &__item-name {
        font-size: 20px;
        white-space: normal;
        position: absolute;
        top: 17px;
        left: 27px;
        right: 5px;
        z-index: 10;
        font-weight: 900;
    }
    // &__item-img{
    //     display: block;
    //     height: 120px;
    // }
}
.faq-nav-scroller {
    @include min(1100){
        overflow: hidden !important;
    }
}

.faq-tab{
    display: none;
    &.isVisible{
        display: block;
    }
}
.loader{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(#fff, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 20;
    pointer-events: none;
    opacity: 0;
    transition: 0.1s;
    &.isLoading{
        opacity: 1;
    }

    &__img{
        @include size(50px);
        img{
            display: block;
        }
    }
}
