.faq-top-tab{
    position: relative;
    padding: 40px 0 35px;
    color: #fff;
    overflow: hidden;
    .faq-box{
        max-width: 960px;
        margin: 0 auto;
    }
    &__bg{
        position: absolute;
        top: -15px;
        left: -15px;
        right: -15px;
        bottom: -15px;
        // @include size(100%);
        z-index: 5;
        background-size: cover;
        background-repeat: no-repeat;
        background-color: #484a4c;
        filter: blur(6px);
        &::after{
            position: absolute;
            top: 0;
            left: 0;
            @include size(100%);
            z-index: 1;
            content: '';
            background: #1c1e21;
            opacity: 0.8;
        }
    }

    .container{
        position: relative;
        z-index: 10;
    }
}


.acordeon{
    $break: 767;
    overflow: hidden;
    position: relative;
    background: #2b2f3c;
    margin-bottom: 35px;
    margin-left: 25/960*100%;
    &:hover{
        &::after{
            border-color: $warning;
        }
    }
    
    &__img{
        cursor: pointer;
        position: relative;
        pointer-events: none;
        overflow: hidden;
        @include max($break - 1){
            height: 40px;
        }
        @include min($break){
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            width: 185px;
        }
        img{
            width: 100%;
        }
        span{
            position: absolute;
            top: 0;
            left: 0;
            padding: 7px 5px 5px 20px;
            font-weight: 300;
            font-size: 17px;
            min-height: 40px;
            z-index: 10;
        }

        &::after{
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            z-index: 5;
            background: linear-gradient(
                to bottom, 
                rgba(0,0,0,0.2) 0%, 
                rgba(0,0,0,0.75) 20px,
                rgba(0,0,0,1) 40px,
                 rgba(0,0,0,1) 100%);
        }
        
    }
    &__head{
        cursor: pointer;
        display: flex;
        align-items: center;
        min-height: 40px;
        padding-left: 10px;
        
        @include min($break){
            padding-left: 210px;
        }
    }
    &__title{
        font-size: 19px;
        line-height: 23px;
        padding: 9px 0;
    }
    &__body{
        font-size: 19px;
        font-weight: 300;
        color: #c9c9c9;
        padding: 10px;
        @include min($break){
            padding: 0 30px 13px 0;
            padding-left: 210px;
        }

        a{
            text-decoration: underline;
            color: $warning;
            &:hover{
                text-decoration: none;
            }
        }
    }
    &__icon {
        margin-left: auto;
        padding: 10px;
        .icon{
            display: block;
            transition: transform 0.3s;
            .isOpened &{
                transform: rotate(180deg)
            }
            .acordeon__head:hover &{
                @extend .icon-arrow-down-h
            }
        }
    }
}
