$navBreak: 1300;

.header {
  // padding: 8px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(#131313, 0.7);
  z-index: 1000;
  border-bottom: 1px solid rgba(#fff, 0.15);

  @include max($navBreak - 1) {
    padding: 13px 0;
  }
  .row {
    align-items: center;
  }

  .container {
    @include min(1300) {
      max-width: 1280px;
    }
    @include between(992, 1200) {
      max-width: 100%;
    }
  }
}

.city-choose-lg {
  @include max($navBreak - 1) {
    display: none;
  }
}
.city-choose-sm {
  @include min($navBreak) {
    display: none;
  }
}

.header-nav {
  flex-grow: 1;
  text-align: right;
}

.menu-btn {
  background: none;
  color: $accent;
  padding: 0;
}

.menu-open {
  @include min($navBreak) {
    display: none;
  }
  .navOpened & {
    display: none;
  }
}
.menu-close {
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 20;
  transform: translateX(90px);
  transition: 0.2s;
  .navOpened & {
    transform: translateX(0);
  }
  @include min($navBreak) {
    display: none;
  }
}

.main-nav {
  &__logo {
    margin-bottom: 10px;
    @include min($navBreak) {
      display: none;
    }
  }

  @include max($navBreak - 1) {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    background: none;
    transition: 0.3s ease;
    pointer-events: none;
    opacity: 0;
    text-align: left;

    body.navOpened & {
      background-color: rgba(#131313, 0.7);
      pointer-events: auto;
      opacity: 1;
    }
  }

  &__inner {
    @include max($navBreak - 1) {
      padding: 15px 0;
      width: 260px;
      height: 100%;
      overflow: auto;
      color: #fff;
      background-color: rgba(19, 19, 19, 1);
      transition: 0.3s ease;
      border-right: 1px solid rgba(#fff, 0.15);
      transform: translateX(-110%);

      body.navOpened & {
        transform: translateX(0);
      }
    }
    @include min($navBreak) {
      text-align: right;
      white-space: nowrap;
    }

    .city-chooser {
      .current:not(:hover) {
        color: #131313;
      }
    }
    .nice-select.city-chooser::after {
      border-color: currentColor;
    }
  }

  &__list {
    font-size: 0;

    @include max($navBreak - 1) {
      display: flex;
      flex-direction: column;
    }
    li {
      vertical-align: top;
      font-size: 16px;
      position: relative;
      @include min($navBreak) {
        display: inline-block;
      }

      &:hover {
        .sub-menu {
          opacity: 1;
          pointer-events: auto;
          transform: scale(1) translateY(0);
        }
      }
    }

    > li {
      padding: 8px 7px;
      border-right: 1px solid rgba(#fff, 0.15);

      @include max($navBreak - 1) {
        border-right: none;
        padding: 8px 15px;
        &:not(:last-child) {
          border-bottom: 1px solid rgba(#fff, 0.15);
        }
      }
    }
    a {
      display: block;
      font-weight: 600;
      position: relative;
      line-height: 36px;

      @include min($navBreak) {
        color: #e5e7ec;
      }
      &::after {
        transition: 0.2s;
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        transform: scaleX(0);
        background-color: $accent;
        height: 2px;
        @include max($navBreak - 1) {
          content: none;
        }
      }
      &:hover,
      &.isCurrent {
        color: $accent;
        &::after {
          transform: scaleX(1);
        }
      }
    }
  }
}
.toOrder {
  background-color: rgba(#14d500, 0.2);
}
.hasSubNav {
  padding: 8px 3px 8px 0;
  position: relative;

  @include max($navBreak - 1) {
    order: 2;
  }

  // .icon{
  //     position: absolute;
  //     @extend .icon-arrow-down;
  //     top: 17px;
  //     right: 10px;
  //     transform: scale(0.7);
  //     @include min($navBreak){
  //         display: none;
  //     }
  // }
  > a {
    display: flex;
    align-items: center;
    color: #14d500;
    position: static;
    &::before,
    &::after {
      content: "\2022";
      transform: translateY(1px);
      width: auto;
      background: none;
      height: auto;
      position: static;
      transition: none;
    }
    &:hover {
      &::after {
        transform: translateY(1px);
      }
    }
  }
}

.sub-menu {
  @include min($navBreak) {
    position: absolute;
    left: -1px;
    // width: 100%;
    right: -1px;
    top: 100%;
    background-color: rgba(19, 19, 19, 0.7);
    padding: 0;
    // border-radius: 5px;
    border: 1px solid rgba(#fff, 0.15);
    transform: scale(0.75) translateY(-21px);
    opacity: 0;
    pointer-events: none;
    transition: all 0.2s cubic-bezier(0.5, 0, 0, 1.25), opacity 0.15s ease-out;
    text-align: left;

    li {
      border-bottom: 1px solid rgba(#fff, 0.15);
      padding: 7px 8px;
    }

    a {
      padding-left: 8px;
      line-height: 24px;
      &::after {
        width: 3px;
        height: 100%;
        top: 0;
        left: 0;
        transform: scaleY(0);
      }

      &:hover,
      &.isCurrent {
        &::after {
          transform: scaleY(1);
        }
      }
    }
  }

  // @include max($navBreak - 1){
  //     display: none;
  // }

  li {
    display: block;

    @include max($navBreak - 1) {
      &::before {
        content: "";
        @include size(5px);
        border-radius: 100%;
        background-color: currentColor;
        position: absolute;
        right: 105%;
        top: 16px;
      }
    }
  }
}

.header-logo {
  $break: 767;
  flex-grow: 0;
  flex-shrink: 0;
  transition: 0.2s;

  &:hover {
    transform: scale(1.05);
  }

  a {
    display: block;
    width: 105px;
  }
  .hide-on-lg {
    @include min($break) {
      display: none;
    }
  }
  .hide-on-sm {
    @include max($break - 1) {
      display: none;
    }
  }
}

.lang-switch {
  flex-grow: 0;
  flex-shrink: 0;

  &__list {
    white-space: nowrap;
    li {
      display: inline-block;
      vertical-align: top;
      font-size: 16px;
      + li {
        margin-left: 5px;
      }
    }
    a {
      font-weight: 600;
      color: #c4c8d4;
      display: block;
      line-height: 36px;

      &:hover,
      &.isCurrent {
        color: $accent;
      }
    }
  }
}
