// $family - string - font-family
// $folder - font folder == font-family
// $font - font file name
// $weight - font-weight
// $style - font-style

@mixin font($family, $font, $weight: 400, $style: normal, $localFamily: $family,  $localFamily2: $family) {
  @font-face {
    font-family: $family;
    src: 
        //  local($localFamily), 
        //  local($localFamily2),
         url('/fonts/#{$font}.woff2') format('woff2'),
         url('/fonts/#{$font}.woff') format('woff');
        //  url('../fonts/#{$folder}/#{$font}/#{$font}.ttf')  format('truetype'); /*      Android,   */
    font-weight: $weight;
    font-style: $style;
  }
}    

@include font('FuturaPt', 'FuturaPT-Book', 400);
@include font('FuturaPt', 'FuturaPT-Heavy', 800);
@include font('FuturaPt', 'FuturaPT-Medium', 500);
@include font('FuturaPt', 'FuturaPT-Demi', 600);
@include font('FuturaPt', 'FuturaPT-Bold', 700);
@include font('FuturaPt', 'FuturaPT-Light', 300);



