button,
input[type="submit"] {
  cursor: pointer;
}

label {
  display: block;
  cursor: pointer;
  user-select: none;
}

select {
  opacity: 0;
}

textarea {
  resize: vertical;
  min-height: 150px;
  height: 100%;
}

.nice-select{
  width: 100%;
  float: none;
  border: none;

  &.city-chooser{
    background: none;
    height: 36px;
    line-height: 36px;
    font-weight: 600;
    font-size: 16px;
    display: inline-block;
    width: auto;
    min-width: 95px;

    @include max($navBreak - 1){
      border: 1px solid #ccc;
      width: 100%;
      border-radius: 3px;
    }

    &::after{
      border-color: #fff;
    }
    &.open{
      .current{
        color: $warning;
      }
    }
    .current{
      transition: 0.2s;
      &:hover{
        color: $warning;
      }
    }
    .current{
      color: #fff;
    }

    .list{
      min-width: 100px;
    }
  }
  
  &.form-control{
    background: #ececec;
    height: 60px;
  }

  .list{
    transform: scale(.75) translateY(-21px);
    border-radius: 3px;
    margin-top: 10px;
    li{
      padding: 0 10px;
    }
  }

  &.filter-select{
    border: 1px solid rgba(#fff, 0.15);
    background: none;
    font-size: 16px;
    height: 40px;
    line-height: 38px;
    max-width: 300px;
    display: inline-block;
    margin: 0 0 20px 0;
    .current{
      color: #fff;
    }

    .list{
      min-width: 200px;
    }

    &:after{
      border-color: #fff;
    }
  }
}

.form-control{
  font-size: 18px;
}

.dropdown{
  font-size: 19px;
  color: #fff;  
  outline: none;
  display: inline-block;
  text-align: left;
}
.dropdown-toggle{
  outline: none;
  width: 305px;
  text-align: left;
  max-width: 100%;
  background: none;
  color: #fff;  
  border: 1px solid rgba(#fff, 0.15);
  padding:  5px 50px 5px 30px;
  &:focus{
    outline: none;

  }
  &::after{
    position: absolute;
    top: 50%;
    right: 35px;
    margin-top: -3px;
  }
}
.dropdown-menu{
  border-radius: 0;
  background-color: #131313;
  padding: 0;
  margin: 0;
  left: 0px !important;
  right: 0px !important;

  border: 1px solid rgba(#fff, 0.15);
}
.dropdown-item{
  background-size: cover;
  color: #fff;
  font-size: 19px;
  padding: 7px 10px;

  &::after{
    z-index: 1;;
    position: absolute;
    top: 0; left: 0;right: 0;
    bottom: 0;
    background-color: #000;
    opacity: 0.1;
    content: '';
  }

  &:not(:last-child){
    border-bottom: 1px solid rgba(#fff, 0.15);
  }

  span{
    position: relative;
    z-index: 5;
    padding-left: 10px;
    &::before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      border-left: 2px solid transparent;
      z-index: 5;
      transition: 0.2s;
    }
  }
  // font-weight: 600;

  &:hover{
    color: $warning;

    span{
        &::before{
          border-color: $warning;
        }
    }
  }
}


