.btn {
  font-size: 17px;
  padding-top: 9px;
  min-height: 56px;
  padding-bottom: 9px;
  display: inline-flex;
  align-items: center;
  justify-content: center;

  .btn-icon {
    font-size: 23px;
    &:first-child {
      margin-right: 8px;
    }
    &:first-child {
      margin-right: 8px;
    }
  }

  * {
    pointer-events: none;
  }
}
.btn-sm {
  border-radius: 0;
  min-height: 42px;
  padding: 5px 30px;
  line-height: 1;
}
.btn-dark {
  &:hover {
    color: $warning;
  }
}

.btn-primary {
  background-color: #fff;
  color: #131313 !important;
  border-color: #fff;

  &:hover,
  &:not(:disabled):not(.disabled):active,
  &:focus,
  &:active {
    background-color: $warning;
    border-color: $warning;
    color: #fff !important;
  }
}

.btn-outline-primary {
  color: #fff;
  background-color: transparent;
  background-image: none;
  border-color: #fff;

  &:hover,
  &:focus,
  &:active {
    background: none;
    color: $warning;
    border-color: $warning;
  }
}

.tooltip-btn {
  border: none;
  background: none;
  padding: 0;
  position: relative;

  &::after {
    content: "";
    display: block;

    @extend .icon-tooltip;
  }

  &:hover {
    &::after {
      @extend .icon-tooltip-h;
    }
  }

  &__text {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: calc(100% + 5px);
    border: 1px solid rgba(#fff, 0.15);
    background-color: rgba(#000, 0.9);
    color: #fdfbfb;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;
    width: 200px;
    padding: 13px;
    text-align: left;
    opacity: 0;
    pointer-events: none;
    transition: 0.2s;

    p {
      margin-bottom: 0.4em;
    }
  }

  &:hover {
    .tooltip-btn__text {
      opacity: 1;
      pointer-events: auto;
    }
  }
}
