//==========COLORS==========//
$background-main: #1b1e23;
$font-color: #fff;
$accent: #ff6000;

//==========FONT==========//
$sans-serif: "FuturaPT", sans-serif;
$root-font-size: 14;
$font-family: "FuturaPT", Helvetica, Helvetica Neue, Arial, Calibri, sans-serif;

//shadows
$shadow: 0px 2px 2.4px 0.6px rgba(51, 51, 52, 0.2);
$shadow-1: 4px 7px 23px 0px rgba(172, 173, 173, 0.3);

$warning: $accent;
$dark: #191919;
$primary: #58b9e2;
$success: #70c280;
$btn-padding-y: 6px;
$btn-padding-x: 50px;
$btn-border-width: 2px;
$btn-font-weight: 700;
$btn-border-radius: 0;
$modal-inner-padding: 50px;

$input-padding-y: 17px;
$input-padding-x: 28px;
// $input-line-height:                     $input-btn-line-height !default;

$input-bg: #ececec;
$input-color: #141414;
$input-border-color: transparent;
$input-border-width: 0px;
$input-box-shadow: none;
$input-border-radius: 0;
$input-placeholder-color: #141414;
$form-group-margin-bottom: 10px;

$slider-break: 767;
