  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.fiter-shadow {
  filter: drop-shadow(1px 1px 0px #000);
}

.link-figure {
  $this: &;
  position: relative;
  display: block;
  overflow: hidden;
  flex: 1;
  @extend .bordered-shadow;
  background-color: #26242a;
  transition: 0.15s ease;
  &__play-box {
    position: absolute;
    top: 0;
    left: 0;
    @include size(100%);
    pointer-events: none;
  }
  &__img {
    overflow: hidden;
    img {
      display: block;
      transition: 0.5s ease;
      width: 100%;
      @supports (object-fit: cover) {
        object-fit: cover;
        height: 285px;
        &.slier-img {
          height: 425px;
        }
        @include max(991) {
          height: 235px;
          &.slier-img {
            height: 425px;
          }
        }
        .link-figure--sm & {
          height: 235px;
        }
      }
    }
  }
  &__figcaption {
    position: absolute;
    z-index: 10;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgba(#131313, 0.7);
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 14px 20px;
    transition: 0.4s;
    .ttl {
      font-weight: 800;
      transition: 0.2s;
      line-height: 1;
      font-family: $sans-serif;
      @include min(767) {
        font-size: 17px;
      }
      &:hover {
        color: $accent;
      }
    }
    .time {
      font-weight: 300;
    }
    .date {
      color: #ececec;
      white-space: nowrap;
      font-size: 20px;
      margin: 0 5px 0 auto;
      svg {
        vertical-align: baseline;
        margin-right: 5px;
      }
    }
    .tag {
      font-weight: 300;
    }
    .more[title] {
      &::before {
        content: attr(title);
        transition: transform 0.4s, opacity 0.4s;
        opacity: 0;
        position: relative;
        top: -1px;
        transform: translateY(-5px);
        #{$this}:hover & {
          opacity: 1;
          transform: translateY(0);
        }
      }
    }
    .full-txt {
      font-size: 25px;
      line-height: 23px;
    }
    .full-txt-dark {
      font-size: 25px;
      line-height: 23px;
      font-weight: 500;
      color: black !important;
    }
    .slide-ttl {
      margin-bottom: auto;
      font-size: 20px;
    }
    .tag {
      font-size: 20px;
    }
    &.slide-caption {
      min-height: 115px;
    }
    &--center {
      top: 0;
      height: 100%;
      background: none;
      #{$this}:not(:hover) & {
        background: none;
      }
    }
    &--top {
      align-items: flex-start;
      top: 0;
    }
    &--top-light {
      align-items: flex-start;
      background: none;
      top: 0;
      line-height: 23px;
    }
      .text-warning {
      font-size: 0.85em;
      line-height: 18px;
      }
      &--white {
        color: white;
        }
    &--bottom {
      display: block;
      background: none;
      color: #171717;
      position: static;
      height: auto;
      .date {
        color: rgba(#000000, 0.7);
      }
      .date,
      .more {
        position: static;
      }
      .ttl {
        margin-bottom: 5px;
        display: block;
      }
      #{$this}:hover & {
        background: none;
      }
    }
  }
}

a.link-figure {
  &:hover {
