.schedule {
  padding-bottom: 15px;
  &__top {
    flex-wrap: wrap;
    display: flex;
  }
  &__top-nav {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__top-nav-icon {
    border: 1px solid rgba(#fff, 0.15);
    padding: 6px 13px 7px;
    margin-bottom: 35px;
    @include max(767) {
      margin-right: 10px;
    }
    .svg-icon {
      display: block;
    }
  }
  &__prices-types {
    font-size: 20px;
    margin-bottom: 27px;
    margin-right: 30px;
    span {
      &:not(:last-child) {
        margin-right: 25px;
      }
    }
  }
  &__weeks {
    display: flex;
    justify-content: center;
    margin-bottom: 25px;
    li {
      font-size: 20px;
      color: rgba(#f8f4f4, 0.5);
      position: relative;
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      &::before {
        content: "";
        flex-shrink: 0;
        border-bottom: 1px solid rgba(#fff, 1);
        width: 23px;
      }
      @include max(767) {
        &::before {
          content: none;
        }
        &:not(:last-child) {
          margin-right: 10px;
        }
      }
      &:active,
      &.active,
      &:hover {
        a {
          color: #fff;
        }
      }
      a {
        display: block;
        border: 1px solid rgba(#fff, 0.15);
        padding: 0 15px;
      }
      .svg-icon {
        margin-right: 8px;
      }
    }
  } //   .slick-slide + .slick-slide {
  // .schedule-slide .date {
  //   border-left: 1px solid #ececec;
  // }
  //   }
  &__table {
    .slick-list {
      padding: 0 3px;
    }
    .slick-slide {
      &:last-child {
        transform: translateX(-2px);
      }
    }
  }
  .tab-content {
    position: relative;
    padding: 0;
  }
  .tab-pane {
    margin-top: 0;
  }
}

.checked {
  &::after {
    content: attr(data-tooltip) "\25b8";
    font-size: 14px;
    line-height: 1;
    position: absolute;
    text-shadow: none;
    bottom: 0;
    left: 0;
    right: 0;
    transition: 0.2s;
    opacity: 0;
    transform: translateY(-3px);
    color: $font-color;
  }
}

.checkedActive {
  &::after {
    opacity: 1;
    transform: translateY(0);
  }
}

.times {
  padding: 0 5px;
}

.time {
  position: relative;
  font-size: 18px;
  display: block;
  text-align: center;
  line-height: 1;
  &:not(:last-child) {
    margin-bottom: 26px;
  }
  &.disabled {
    pointer-events: none;
  }
  &__tooltip {
    position: absolute;
    right: 4px;
    bottom: 4px;
    opacity: 0;
    transition: 0.2s;
    .time:hover & {
      opacity: 1;
    }
    .tooltip-btn__text {
      z-index: 100;
      width: 130px;
      transform: none;
      right: 0;
      left: auto;
    }
  }
  &__txt {
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 8px;
    margin-top: 3px;
  }
}

.time-control {
  max-width: 130px;
  position: relative;
  font-size: 18px;
  margin: 0 auto;
  background-color: #232323;
  border: 1px solid #343434;
  padding: 3px 0;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.7);
  &--pg {
    min-height: 130px;
  }
  .disabled & {
    box-shadow: none;
    border-color: transparent;
    background: none;
    pointer-events: none;
    color: #a1a1a1;
  }
  &:hover,
  .time-input:checked + & {
    background-color: #2f2f2f;
    border-color: #4a4a4a;
  }
  &::after {
    content: attr(data-tooltip);
    font-size: 14px;
    line-height: 1;
    position: absolute;
    text-shadow: none;
    bottom: 100%;
    padding-bottom: 3px;
    left: 0;
    right: 0;
    transition: 0.2s;
    opacity: 0; // transform: translateY(-3px);
    color: $font-color;
  }
  &:hover {
    @extend .checkedActive;
  }
}

.time-input {
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
  &:checked {
    + .time-control {
      @extend .checkedActive;
    }
  }
}

.schedule-slide {
  text-align: center; //   box-shadow: 0 0 0 1px rgba(#fff, 0.15) inset;
  box-shadow: 0 0 0 1px rgba(#fff, 0) inset;
  transition: 0.3s;
  padding-bottom: 10px;
  &.past {
    pointer-events: none;
  }
  &.today {
    background-color: rgba(#000, 0.2);
    box-shadow: 1px 5px 5px 0px rgba(0, 0, 0, 0.7) inset;
    &:hover {
      box-shadow: 1px 5px 5px 0px rgba(0, 0, 0, 0.7) inset;
    }
  }
  &:hover {
    box-shadow: 0 0 0 1px rgba(#fff, 0.15) inset;
  }
  .date {
    // background-color: #f9f9f9;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    min-height: 105px;
    line-height: 1;
    padding: 20px 5px;
    margin-bottom: 5px;
    .today-ttl {
      margin-bottom: 10px;
    }
    strong {
      font-size: 20px;
      font-weight: 400;
      display: block;
      margin-top: 3px;
      text-transform: capitalize;
    }
    span {
      font-size: 15px;
      color: #d5d5d5;
    }
  }
}

