    background-color: #ececec;
    width: 240px;
    padding: 15px 20px;
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -120px; // transform-origin: 50% 100%;
    transition: 0.2s;
    transform: scale(0.97);
    pointer-events: none;
    opacity: 0;
  }
  &:hover {
    #{$this}__btn {
      color: $warning;
      opacity: 1;
    }
