.kit {
  padding: 15px;
  text-align: center;
  background-color: #f1f1f1;
  width: 100%;
  $s: &;
  @include min(992) {
    padding: 20px 15px 20px 35px;
  }
  &:not(:last-child) {
    margin-bottom: 35px;
  }
  &__ttl {
    font-weight: 600;
    font-size: 25px;
    text-align: left;
    margin-bottom: 27px;
    .num {
      font-weight: 700;
      margin-right: 10px;
    }
  }
  &__main-row {
    // align-items: center;
    margin: auto 0;
    @include min(500) {
      display: flex;
    }
    &.single {
      padding-bottom: 35px;
    }
  }
  &__left-col {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    @include max(499) {
      margin-bottom: 15px;
    }
  }
  &__lg-col {
    display: flex;
    flex-direction: column;
    @include min(500) {
      width: 25%;
    }
  }
  &__inner-row {
    display: flex;
    align-items: center;
    @include max(499) {
      flex-wrap: wrap;
    }
    &:not(:last-child) {
      margin-bottom: 15px;
      @include min(500) {
        margin-bottom: 35px;
      }
    }
  }
  &__inner-txt {
    width: 80px;
    font-size: 19px;
    font-weight: 600;
    @include max(499) {
      width: 100%;
      margin-bottom: 5px;
    }
    @include min(500) {
      margin-right: 20px;
    }
    &.aligned {
      @include min(500) {
        padding-bottom: 30px;
      }
    }
  }
  &__box {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  &__box-icon {
    display: flex;
    align-items: center;
    margin: 0 10px;
    @include max(499) {
      display: none; // .kit__box-icon{
      // }
      + #{$s}__box {
        margin-left: 10px;
      }
    }
    @include max(499) {
    }
    .icon {
      flex-shrink: 0;
    }
  }
  .d-flex {
    flex: 1;
  }
}

.kit-box {
  color: #fff;
  min-height: 100px;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  position: relative;
  align-items: center;
  padding: 10px 5px;
  flex: 1;
  box-shadow: 1px 2px 8.46px 0.54px rgba(0, 0, 0, 1);
  $s: &;
  &__more {
    color: #fff;
    position: absolute;
    bottom: 5px;
    right: 5px;
    opacity: 0;
    transition: inherit;
    #{$s}:hover & {
      opacity: 1;
    }
    &:hover {
      color: $warning;
    }
  }
  &__footer {
    padding-top: 8px;
    @include max(499) {
      margin-bottom: 15px;
    }
  }
  &--height {
    @include min(500) {
      min-height: 235px;
    }
  }
  &--height-1 {
    @include min(500) {
      height: 370px;
    }
  }
}

.kit-box-txt {
  font-size: 19px;
  font-weight: 900;
  &.lg {
    @include min(992) {
      font-size: 25px;
    }
  }
}

