.main__clipper {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.main__scroller {
  overflow-x: scroll;
  overflow-y: hidden;
  height: 100%;
  white-space: nowrap;
  max-height: 185px !important;
  min-height: 185px !important;
}

.main__scroller::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.main__bar {
  position: absolute !important;
  bottom: 3px;
  width: 10px;
  height: 7px;
  background-color: $warning;
  z-index: 10;
  transition: background-color 0.2s; // border-radius: 2px;
  @extend .border-white;
  &:hover {
    background-color: lighten($warning, 10%);
  }
}

