.card-info {
  color: #fff;
  &__list {
    padding: 13px 0;
    @extend .text-shadow;
    font-size: 20px;
    &--decimal {
      counter-reset: list;
      li {
        counter-increment: list;
        padding-left: 60px;
        position: relative;
        &::before {
          content: counter(list);
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 15px;
          font-size: 35px;
        }
      }
    }
    li {
      margin-bottom: 10px; // line-height: 1;
    }
    a {
      transition: none;
      position: relative;
      padding-left: 50px;
      display: block;
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
      }
      &:hover {
        color: $warning;
      }
    }
    .team-building {
      &::before {
        @extend .icon-party;
      }
      &:hover {
        &::before {
          @extend .icon-party-h;
        }
      }
    }
    .birthday {
      &::before {
        @extend .icon-glases;
      }
      &:hover {
        &::before {
          @extend .icon-glases-h;
        }
      }
    }
    .night {
      &::before {
        @extend .icon-moon;
      }
      &:hover {
        &::before {
          @extend .icon-moon-h;
        }
      }
    }
    .tournament {
      &::before {
        @extend .icon-trophy-1;
      }
      &:hover {
        &::before {
          @extend .icon-trophy-1-h;
        }
      }
    }
    .table-games {
      &::before {
        @extend .icon-bones;
      }
      &:hover {
        &::before {
          @extend .icon-bones-h;
        }
      }
    }
    .spec {
      &::before {
        @extend .icon-puzzle;
      }
      &:hover {
        &::before {
          @extend .icon-puzzle-h;
        }
      }
    }
  }
  &__bonus {
    font-size: 20px;
    @extend .text-shadow;
    position: relative;
    padding-left: 60px;
    margin-bottom: 30px;
    &::before {
      content: attr(data-text);
      position: absolute;
      top: 50%;
      transform: translateY(-50%) rotate(-35deg);
      left: 0;
      text-transform: uppercase;
      color: $warning;
      font-size: 16px;
    }
  }
  > p {
    font-size: 22px;
    line-height: (25/22);
  }
  .btn {
    min-height: 52px;
    padding: 0 20px;
    margin-bottom: 20px;
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  .tooltip-btn__text {
    left: 100%;
    top: 0;
    bottom: auto;
    transform: none;
    z-index: 1000;
    @include max(479) {
      right: 0;
      top: 100%;
      left: auto;
    }
  }
}

.section-shop {
  background: transparent url(../img/images/bg-shop-section.jpg) no-repeat left
    top / cover;
  &__img {
    @include max(766) {
      display: none;
    }
  }
}

.section.dark-shadow {
  &.dark-bg--2 {
    z-index: 2;
    .isHome & {
      position: relative;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        border-bottom: 1px solid rgba(#fff, 0.15);
        border-top: 1px solid rgba(#fff, 0.15);
        pointer-events: none;
        z-index: 150;
      }
    }
  }
}

.slider-section {
  &__link {
    text-decoration: underline;
    font-size: 20px;
    margin-top: 15px;
    display: block;
    color: #fff;
    font-weight: 400;
    @include min(767) {
      float: right;
    }
    &.job {
      font-size: 18px;
      @include max(1000) {
        float: none;
      }
    }
  }
}

.loyalty-box {
  font-size: 20px;
  @include max(480) {
