.pagination {
  padding: 0;
  margin: 0 0 15px;
  list-style: none;
  display: block;
  li {
    display: inline-block;
    vertical-align: bottom;
    color: #fff;
    margin-bottom: 10px;
    &:not(:last-child) {
      margin-right: 10px;
    }
  }
  a {
    display: block;
    border: 1px solid rgba(#fff, 0.15);
    font-size: 20px;
    font-weight: 600;
    @include size(40px);
    text-align: center;
    line-height: 38px;
    &:hover {
      border-color: #fff;
    }
    &.current {
      color: $warning;
      border-color: $warning;
    }
  }
}

