.processes {
  background-attachment: fixed;
  background-size: cover;
  background-position: 50% 100%;
  .ttl-md {
    margin-bottom: 25px;
  }
  .regimes__tabs {
    margin-bottom: 65px;
  }
}

.process {
  @include min(1025) {
    max-width: 505px;
    margin-left: auto;
    margin-rigth: auto;
  }
  flex: 1;
  display: flex;
  align-items: center;
  border-radius: 5px;
  overflow: hidden;
  border: 1px solid rgba(#fff, 0.07);
  background-color: rgba(#131313, 0.55);
  padding: 16px 20px;
  text-align: center;
  margin-bottom: 37px;
  box-shadow: 3px 4px 4px 0px rgba(0, 0, 0, 0.75);
  @include max(991) {
    flex-direction: column;
  }
  &__icons {
    flex-shrink: 0;
    margin-right: 20px;
    @include max(991) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
  &__icon {
    margin: 5px 0;
    @include max(991) {
      display: inline-block;
    }
  }
  &__inf {
    line-height: 25px;
  }
  &__img {
    margin-bottom: 8px;
    overflow: hidden;
    height: 86px;
    border-radius: 7px;
    border: 1px solid rgba(#fff, 0.07);
    img {
      height: 100%;
      object-fit: cover;
      display: block;
      width: 100%;
    }
  }
  &__ttl {
    font-size: 25px;
  }
  &__txt {
    font-weight: 300;
    font-size: 23px;
  }
}

