.opposition {
  background-image: url(../img/images/zone-2.png);
  background-position: bottom center;
  background-size: cover;
  min-height: 885px;
  padding-top: 300px;
  &__slider {
    max-width: 735px;
    margin: 0 auto;
    position: relative;
    padding: 14px;
    .slick-arrow {
      position: absolute;
      top: 0;
    }
    .slick-prev {
      left: 0;
    }
    .slick-next {
      right: 0;
    }
    @include min(480) {
      .slick-arrow {
        bottom: 0;
        height: 100%;
      }
    }
  }
  &__slide {
    text-align: center;
    @extend .text-shadow;
    line-height: 1;
    font-size: 24px;
    font-weight: 300;
    padding: 25px 15px 20px;
    @include min(480) {
      padding: 25px 50px 50px;
    }
    .text-green {
      color: #25dd17;
    }
  }
  &__slide-top {
    align-items: center;
    @include min(480) {
      display: flex;
    }
    .item {
      flex: 1;
      margin-bottom: 20px;
      padding-block-end: 0 10px;
    }
    .vs-item {
      font-size: 16px;
      font-weight: 300;
    }
    .player-count {
      font-weight: 400;
    }
  }
  &__slide-txt {
    text-shadow: none;
  }
}

.text-light-blue {
  color: #adc8ff;
}

