.regimes-1 {
  color: #000;
  .regimes {
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
  }
  .regimes__row {
    position: relative;
    margin: 0 -7px;
    display: flex;
    flex-wrap: wrap;
  }
  .regimes__col {
    padding: 0 7px;
    flex-grow: 1;
    flex-basis: 290px;
    height: 310px;
    margin-bottom: 14px;
  }
  .regimes .ttl-md {
    color: #fff;
  }
  .regime {
    text-align: center;
    font-size: 18px;
    line-height: 1.33333;
    position: relative;
    flex: 1;
    transition: 0.2s;
  }
  .regime__inner {
    padding: 20px;
    position: absolute;
    width: 100%;
    min-height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 0.4s;
  }
  .regime:hover .regime__inner {
    width: calc(100% + 70px);
    z-index: 10;
  }
  @media (max-width: 991px) {
    .regime:hover .regime__inner {
      width: calc(100% + 30px);
    }
  }
  .regime__img {
    height: 215px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
  }
  .regime__img img {
    height: auto;
    max-height: 100%;
  }
  .regime:hover .regime__img {
    height: 190px;
  }
  .regime__ttl {
    font-size: 25px;
    font-weight: 600;
    margin-bottom: 8px;
  }
  .regime__more-link {
    transition: 0.2s;
    cursor: pointer;
  }
  .regime:hover .regime__more-link {
    opacity: 0;
  }
  .regime__more-txt {
    overflow: hidden;
    transition: height 0.3s, opacity 0.1s;
    transition-delay: 0s, 0.3s;
    height: 0;
    opacity: 0;
  }
  .regime:hover .regime__more-txt {
    height: 100px;
    opacity: 1;
  }
  @media (max-width: 991px) {
    .regime:hover .regime__more-txt {
      height: 120px;
    }
  }
}

