  transition: 0.15s;
  cursor: pointer;
  img {
    width: 100%;
  }
  &:hover {
    transform: scale(1.05);
  }
}

.gallery-section {
  z-index: 100;
}

.gallery-slider {
  opacity: 0;
  transition: opacity 0.2s;
  .gallery-image {
    &:hover {
      transform: none;
    }
  }
}

.modal-gallery .modal-dialog {
  max-width: 100%;

  @include min(767) {
    max-width: 1200px;
  }

  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
  }
  .slick-prev {
    right: auto;
    left: 0;
  }
}

