*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  height: 100%;
  font-size: $root-font-size + px;
  line-height: (23/14);
  // color: $font-color;
}

body {
  min-height: 100%;
  margin: 0;
  font-weight: normal;
  min-width: 320px;
  font-family: $font-family;
  background-color: $background-main;
  @include font_smoothing;
  color: $font-color;

  &.navOpened {
    overflow: hidden;
  }
}

p {
  margin-top: 0;

  &:last-child {
    margin-bottom: 0;
  }
}

a {
  text-decoration: none;
  transition: all 0.3s;
  color: inherit;
  &:hover {
    text-decoration: none;
    color: $accent;
  }
}
sup {
  top: 0;
  font-size: 1.2em;
}
img {
  max-width: 100%;
  height: auto;
}

svg {
  fill: currentColor;
}

.svg-icon {
  display: inline-block;
  vertical-align: middle;
  pointer-events: none;
}

[role="button"] {
  cursor: pointer;
}

.visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
}

.list-unstyled {
  margin-bottom: 0;
}

.bg-grey {
  background-color: #ececec;
}

.icon {
  display: inline-block;
}

@include sprites($spritesheet-sprites);

img.align-left {
  float: left;
  margin: 0 25px 25px 0;
}
img.align-right {
  float: right;
  margin: 0 0 25px 25px;
}
img.align-left,
img.align-right {
  @include max(767) {
    width: 100%;
    float: none;
    margin: 0 0 25px;
  }
}

.hidden {
  display: none !important;
}

