.prod-succes {
    color: #3edc14;
}

.prod-primary {
    color: #14dcc7;
}

.prod-warn {
    color: #ffae00;
}

.border-white{
    position: relative;
    &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        pointer-events: none;
        border: 1px solid rgba(#fff, 0.15);
        z-index: 10;
    }
}

.bordered-shadow {
    box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.75);
    @extend .border-white;
}

.shop {
    background: #724000 url(../img/images/shop-bg1.jpg) no-repeat center center / cover;
    // background: #724000 url(../img/images/shop-bg.jpg) no-repeat top center / cover;
    background-attachment: fixed;
    padding-top: 0;
    &__filter {
        margin-bottom: 29px;
        padding-top: 25px;
    }
}

.nice-select.shop-select {
    display: inline-block;
    color: #fff;
    font-size: 19px;
    height: 42px;
    line-height: 40px;
    border-color: rgba(#fff, 0.15);
    &::after {
        border-color: #fff;
    }
    .option {
        color: $font-color;
    }
}

.products {
    $break: 768;
    $break1: 1100;
    padding-bottom: 30px;
    
    @include min($break1) {
        padding-bottom: 60px;
        max-width: 995px;
        margin: 0 auto;
    }
    &__row {
        @include min($break1) {
            margin: 0 -20px;
        }
    }
    &__col {
        margin-bottom: 20px;
        @include min( $break) {
            margin-bottom: 40px;
        }
        @include min($break1) {
            padding: 0 20px;
        }
    }
}

.product {
    background-color: #000;
}

.product-card {
    cursor: pointer;
    @extend .bordered-shadow;
    transition: 0.15s ease;
    $break: 768;
    @include max( $break) {
        max-width: 340px;
        margin: 0 auto;
    }
    &:hover {
        transform: scale(1.05);
    }
    &__image {
        min-height: 180px;
        @include min( $break) {
            @supports (object-fit: cover){
                min-height: 390px;
                height: 390px;    
            }
        }
    }
    &__text {
        position: absolute;
        z-index: 5;
        bottom: 0;
        left: 0;
        width: 100%;
        padding: 20px;
        display: flex;
        flex-direction: column;
        @include min( $break) {
            min-height: 170px;
        }
    }
    &__ttl {
        color: #fdfbfb;
        font-size: 16px;
        line-height: 21px;
        max-width: 160px;
        margin-bottom: em(5, 16);
        @include min( $break) {
            font-size: 22px;
            line-height: 24px;
            max-width: none;
        }
    }
    &__sub-ttl {
        font-size: 14px;
        line-height: 15px;
        font-weight: 300;
        margin-bottom: em(4, 14);
        @include min( $break) {
            font-size: 20px;
            display: flex;
            >div+div {
                margin-left: 5px;
            }
        }
    }
    &__footer {
        color: #fff;
        margin-top: auto;
        white-space: nowrap;
        small{
            font-size: 16px;
        }
        .price {
            font-weight: 600;
            @include min($break) {
                font-size: 20px;
            }
        }
        .icon {
            vertical-align: bottom;
            margin: 0 5px;
        }

        .box-f{
            @include min($break){
                display: inline-block;
            }
        }
    }

    .link-figure__figcaption{
        line-height: 1;
        padding-bottom: 25px;
    }
}

.product-card-img {
    display: block;
    width: 100%;
    height: auto;
    min-height: 180px;
    @supports (object-fit: cover) {
        height: 100%;
        object-fit: cover;
        object-position: top right;
    }
}