.modal {
  color: #fff;
  .btn {
    width: 100%;
  }
  .form-control {
    border: 1px solid rgba(#fff, 0.15);
    background: #171e31;
    color: #fff;
    padding: 8px 20px;
    font-size: 18px;
    &:focus {
      box-shadow: none;
      border-color: rgba(#fff, 0.3);
    }
    @include placeholder {
      color: #adadad;
    }
  }
  .form-group {
    margin-bottom: 7px;
  }
  textarea {
    max-height: 70px;
    min-height: 70px;
  }
  .nice-select .option {
    color: #0e0e15;
    line-height: 1.4;
  }
  .nice-select {
    height: 44px;
    line-height: 42px;
    padding-top: 0;
    padding-bottom: 0;
    border: 1px solid rgba(#fff, 0.15);
    background: #171e31;
  }
}

.modal-dialog {
  max-width: 700px;
}

.modal-header .close {
  margin: 0;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
  span,
  .svg-icon {
    display: block;
  }
  box-shadow: none;
  &:hover {
    box-shadow: none !important;
    color: $warning;
  }
}

.modal-header {
  position: relative;
  padding-left: $modal-inner-padding;
  padding-right: $modal-inner-padding;
  border: none;
  padding-top: 30px;
}

.modal-content {
  border-radius: 0;
  background-color: #2b2f3c;
}

.modal-body {
  padding-bottom: 30px;
  padding-top: 30px;
}

.modal-backdrop.show {
  opacity: 0.8;
}

.modal-video {
  background: none;
  .modal-content,
  .modal-header {
    border: none;
    background: none;
  }
  .modal-body {
    padding: 0;
  }
  .close {
    top: -39px;
  }
  .modal-dialog {
    margin-top: 40px;
  }
  .modal-header {
    padding: 0;
  }
  iframe {
    width: 855px;
    max-width: 100%;
    height: 250px;
    @include min(500) {
      height: 400px;
    }
    @include min(700) {
      height: 480px;
    }
  }
  .modal-dialog {
    max-width: 855px;
  }
  .close {
    color: #fff;
  }
}

.modal-complect {
  .modal-header {
    display: block;
    line-height: 1;
    padding-bottom: 0;
  }
  .modal-body {
    padding-top: 15px;
  }
  .ttl-md {
    font-size: 35px;
    span {
      font-size: em(30, 35);
    }
  }
  p {
    font-size: 30px;
  }
}

.game {
  &__ttl {
    font-size: 30px;
    line-height: 1.1;
    margin-bottom: 20px;
  }
  &__list {
    line-height: 1.4;
    li {
      margin-bottom: 15px; // &:not(:last-child){
      // }
    }
    .key {
      font-size: 16px;
    }
    .val {
      font-size: 20px;
      font-weight: 600;
      span {
        font-weight: 400;
        font-size: 18px;
        color: #a0a0a0;
        margin-left: 5px;
      }
    }
  }
}

.modal-thanks {
  .txt {
    font-weight: 600;
    line-height: 1.3;
    text-align: center;
    font-size: 20px;
    @include min(480) {
      font-size: 25px;
    }
    @include min(767) {
      font-size: 30px;
    }
  }
  .btn {
    max-width: 317px;
    display: block;
    padding-top: 9px;
    margin: 30px auto 15px;
  }
}

.footnote {
  line-height: (21/14);
  font-size: 14px;
  font-weight: 600;
  @include max(576) {
    margin-bottom: 15px;
  }
}

.modal-order {
  min-height: 275px;
  .modal-header {
    justify-content: center;
    .ttl-md {
      font-size: 35px;
    }
  }
  .thanks {
    padding-left: 0;
    padding-right: 0;
  }
  .order-inner {
    min-height: 335px;
  }
}

.product-modal {
  color: #ffffff;
  margin: 10px auto;
  &.startInit {
    display: block;
    pointer-events: none;
    z-index: -1000;
  }
  .modal-dialog {
    padding: 0 5px;
    max-width: 430px;
    margin: 0.5rem auto;
    @include min(767) {
      max-width: 925px;
    }
    .product-modal-slider {
      padding: 0 10px;
      @include min(575) {
        padding: 0 40px;
      }
      @include min(992) {
        padding: 0 70px;
      }
    }
  }
  .modal-content {
    background: none;
  }
  .modal-body {
    padding: 0;
  }
  &__slider {
    flex-shrink: 0;
    background: #212b46;
    box-shadow: 0 0 0 1px rgba(#fff, 0.15) inset;
    @extend .bordered-shadow;
    position: relative;
    z-index: 10;
    @include min(767) {
      width: 335px;
    }
    .product-card__text {
      padding-bottom: 25px;
    }
    .slick-dots {
      position: absolute;
      padding: 5px 15px 15px;
      bottom: 0;
      left: 0;
      width: 100%;
      display: flex;
      z-index: 100;
      li {
        padding: 0 4px;
        flex: 1;
      }
      button {
        width: 100%;
        &::after {
          content: none;
        }
      }
    }
  }
  &__slide {
    box-shadow: none;
    cursor: default !important;
    @include max(766) {
      max-width: 100% !important;
    }
    &:hover {
      transform: none !important;
    }
    &::after {
      content: none !important;
    }
    .product-card__image,
    .product-card-img {
      display: block !important; // min-height: 180px;
      @include min(767) {
        @supports (object-fit: cover) {
          min-height: 430px;
          height: 430px;
        }
      }
    }
  }
  &__content {
    margin: 0 7px;
    background: #212b46;
    box-shadow: 0 0 0 1px rgba(#fff, 0.15) inset;
    @include min(767) {
      position: relative;
      width: calc(100% - 335px);
      margin: 30px 0;
    }
  }
  &__tooltip {
    // @include min(767){
    // 	display: inline-block;
    // }
    @include max(766) {
      width: 100%;
      text-align: center;
      .tooltip-btn__text {
        right: auto;
        transform: translateX(-50%);
        left: 50%;
      }
    }
  }
  &__close-btn {
    color: #fff;
    position: absolute;
    top: 0;
    right: 2px;
    padding: 4px !important;
    line-height: 1;
    z-index: 100;
    &:hover {
      color: $warning;
    }
    span,
    svg {
      display: block;
    }
  }
}

.product-modal-slider {
  opacity: 0;
  transition: 0.2s;
  &.in {
    opacity: 1;
  }
}

.product-modal-box {
  position: relative;
  align-items: center;
  @include min(767) {
    display: flex;
    width: 775px;
  }
}

.thanks {
  padding: 32px 35px;
  position: relative;
  &__ttl {
    font-size: 27px;
  }
  &__txt {
    font-size: 19px;
    font-weight: 300;
  }
  &__link {
    display: block;
    font-size: 23px;
    margin-bottom: 10px;
  }
  small {
    font-size: 16px;
    display: block;
    font-weight: 300;
  }
}

.product-modal-form {
  // padding: 20px;
  position: relative;
  flex-grow: 1;
  .btn {
    height: 42px;
    min-height: 42px;
    padding: 0;
    line-height: 38px;
  }
  .footnote {
    font-weight: 300;
  }
  .sbm {
    padding-top: 7px;
  }
}

.product-modal-content {
  padding: 20px;
  display: flex;
  @include min(767) {
    min-height: 370px;
  }
  &__inner {
    display: flex;
    overflow: hidden;
    flex-direction: column;
    flex: 1;
  } // .tooltip-btn__text{
  //     @include max(766){
  //         left: auto;
  //         right: 0;
  //         transform: none;
  //     }
  // }
  &__scroll-text {
    position: relative;
    font-weight: 300;
    font-size: 14px;
    line-height: 22px;
    height: 100%;
    margin-bottom: 10px;
    max-height: 150px;
    @include min(767) {
      font-size: 20px;
      max-height: 185px;
      line-height: 24px;
      .index-new & {
        max-height: 250px;
        p {
          font-weight: 300;
          font-size: 20px;
        }
      }
    }
  }
  &__footer {
    margin-top: auto;
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-bottom: 13px;
    a,
    button {
      max-height: 42px;
      margin: 5px 0;
      &:not(:last-child) {
        margin-right: 20px;
      }
    }
    .btn {
      display: inline-block;
      max-width: 120px;
      padding: 0;
      min-width: 42px;
      line-height: 38px;
    }
    @include max(766) {
      flex-wrap: wrap;
      justify-content: space-between;
      .btn,
      a {
        width: calc(50% - 10px);
        margin-right: 0 !important;
        max-width: 100%;
      }
    } // @include max(574){
    // 	.btn,a {
    // 		flex-grow: 1;
    // 		width: 100%;
    // 		margin-right: 0 !important;
    //         max-width: 100%;
    //         flex-grow: 1;
    //     }
    // }
  }
  &__footnote {
    font-weight: 300;
    font-size: 14px;
    line-height: 22px;
    @include min(767) {
      font-size: 20px;
      line-height: 19px;
    }
  }
}

.product-modal-slider {
  .slick-prev {
    left: 0;
  }
  .slick-next {
    right: 0;
  }
  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
}

.baron__scroller {
  height: 100%;
  max-height: 100%;
  min-width: 100% !important;
  max-width: 100% !important;
  width: 100% !important;
}

.baron__track {
  background-color: rgba(#0e0e15, 0.4);
  width: 10px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 10px;
}

.product-modal-scroller {
}

.baron__clipper {
  position: relative;
  overflow: hidden;
  @include size(100%);
}

.baron__scroller {
  overflow-y: scroll;
  -ms-overflow-style: none;
  margin: 0;
  border: 0;
  padding: 0 10px 0 0;
  width: 100%;
  height: 100%;
  -webkit-overflow-scrolling: touch;
  /* remove line to customize scrollbar in iOs */
}

.baron__scroller::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.baron__track {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
}

._scrollbar .baron__track {
  display: block;
}

.baron__free {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.baron__bar {
  display: none;
  position: absolute;
  right: 0;
  z-index: 1;
  width: 6px;
  right: 2px;
  background-color: #fff;
  opacity: 0.15;
  border-radius: 8px;
  &:hover {
    opacity: 0.3;
  }
}

._scrollbar .baron__bar {
  display: block;
}

.baron__control {
  display: none;
}

.modal-blog {
  .modal-dialog {
    max-width: 1135px;
  }
  .modal-content {
    margin-top: 50px;
    position: relative;
    background: #2b2f3c;
    color: #fff;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      @include size(100%);
      border: 1px solid rgba(#fff, 0.15);
      pointer-events: none;
    }
  }
  .btn {
    width: auto;
  }
  .modal-body {
    padding: 20px;
    @include cf;
  }
  p {
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
  }
  h3 {
    font-size: 24px;
    margin-bottom: 1em;
  }
  * {
    text-shadow: none;
  }
  .order {
    padding: 20px 0;
    clear: both;
  }
  .head-img {
    position: relative;
    margin: -20px -20px 20px -20px;
    img {
      width: 100%;
    }
  }
}

.close-article {
  background: none;
  border: none;
  padding: 0 35px 35px 35px;
  font-size: 20px;
  color: #fff;
  position: absolute;
  left: 0;
  bottom: 100%;
  &:hover {
    color: $warning;
    .icon {
      @extend .icon-arrow-white-h;
    }
  }
  .icon {
    margin-right: 5px;
  }
}

///////////////////////////
.modal-complects-slider {
  @include min(575) {
    padding: 0 75px;
  }
}
.modal-complects {
  .modal-content {
    background: none;
  }
  .modal-dialog {
    max-width: 1285px;
  }
  .kit__main-row,
  .kit__main-row.single {
    padding-bottom: 15px;
  }
  .kit__inner-row:not(:last-child) {
    @include min(500) {
      margin-bottom: 15px;
    }
  }
  .kit-box {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 20px;
    font-size: 19px;
    line-height: 20px;
    @extend .text-shadow;
    text-align: left;
    @extend .bordered-shadow;

    &.sm-h {
      min-height: 60px;
    }
  }
  .kit-box-txt {
    span {
      font-weight: 300;
      font-size: 19px;
      display: inline-block;
    }
  }
  .kit-order-link {
    @extend .bordered-shadow;
    font-weight: 300;
    padding: 0;
    height: 85px;
    border: none !important;
    background-color: #191d20;
    font-size: 24px;
    line-height: 1;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    padding-bottom: 30px;

    span {
      &::after {
        text-align: center;
      }
    }
    &.bg-2 {
      background-color: #232323;
    }
  }
  .order-table__col {
    &:not(:last-child) {
      margin-right: 6px;
    }
  }
  .kit__inner-row--sm {
    justify-content: center;
    .kit__box {
      max-width: 220px;
    }
  }
  .order-table__txt,
  .order-table__col > span {
    font-weight: 300;
  }
  .order-table__foot {
    line-height: 1;
    font-weight: 300;
  }
  .old {
    text-decoration: line-through;
    font-size: 18px;
    color: #b8b8b8;

    &::after {
      content: none;
    }
  }
  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 5px;
  }

  .slick-next {
    left: auto;
    right: 5px;
  }
}
.modal-complects {
  .close {
    color: #fff;
    position: absolute;
    top: 3px;
    z-index: 10;
    right: 8px;
    @include min(575) {
      right: 83px;
    }
  }
  .modal-dialog {
    opacity: 0;
    transition: 0.15s;
  }

  .complect {
    padding-bottom: 25px;
  }
}
.modal-complect {
  &__top {
    position: relative;
    @extend .bordered-shadow;
    z-index: 15;
    .slide {
      height: 280px;
      background-size: cover;
      display: block !important;

      @include max(800) {
        background-position: top right;
      }
    }
    .slick-dots {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      li {
        padding: 6px 7px;
      }
    }
    .complect {
      position: absolute;
      top: 0;
      left: 0;
      min-width: 100%;
      min-height: 100%;
      pointer-events: none;
      &:hover {
        transform: none;
      }
    }
  }
  &__bottom {
    @extend .bordered-shadow;
    margin: 0 17px;
    z-index: 5;
    .kit {
      background: none;
    }
  }
  &__scroll {
    overflow: auto;
  }
  &__scroll-inner {
    min-width: 900px;
    .row,
    .kit__main-row {
      flex-wrap: nowrap;
      @extend .d-flex;
      // @extend .align-self-center;
    }
    .left {
      width: 2/3 * 100%;
    }
    .right {
      width: 1/3 * 100%;
    }
    .kit__left-col {
      // justify-content: center;
    }
    .kit-box--aline {
      min-height: 180px;
    }
    .kit__box-icon {
      @include max(499) {
        display: flex;
      }
    }
  }
}
