.footer{
    background-color: #171717;
    color: #f9f9f9;
    font-family: $sans-serif;
    padding-top: 50px;
    position: relative;
    z-index: 20 !important;
    border-top: 1px solid rgba(#fff, 0.15);
    @extend .dark-shadow;

    .isHome & {
    }

    &__title{
        font-size: 25px;
        margin-bottom: 12px;
        font-weight: 600;
    }

    a:hover{
        color: $accent;
    }
}
.footline{
    padding-top: 15px;
    padding-bottom: 15px;
    .copy{
        color: #f9f9f9;
        font-size: 14px;
        @include max(479){
            padding-left: 30px;
        }
    }
}
