.footer-nav-list{
    line-height: (30/18);
    @include max(479){
        padding-left: 15px;
    }
    li{
        &.isActive{
            font-weight: 800;
            color: $accent;
        }
    }
    a{
        font-size: 18px;

        &:hover{
            text-decoration: none;
        }
    }
}

.footer-logo{
    overflow: hidden;
    &:hover{
        a{
            transform: scale(1.01);
        }
    }
   
    a{
        display: block;
        margin-bottom: 30px;
        transition: 0.2s;
        &:hover{
            a{
                transform: scale(1.01);
            }
        }
        @include max(479){
            padding-left: 15px;
        }
    }
}

.other-project{
    display: inline-block;
    padding: 12px;
    transition: 0.15s;
    &:hover{
        transform: scale(1.02);
    }
    &__img{
        @extend .bordered-shadow
    }
}

