.contacts {   
    padding-top: 97px;
    color: #fff;
    background: transparent url(../img/images/bg-contacts.jpg) no-repeat top center / cover;
    background-attachment: fixed;
    @extend .text-shadow;
    
    .ttl-large { 
        margin-bottom: 14px;
        +.ttl-sm {
            margin-bottom: 30px;
        }
    }
    &__map-head{
        background-color: #131313;
        color: #fff;
        background: transparent url(../img/images/bg-contacts-1.jpg) no-repeat top center / cover;
        padding: 11px 15px;
        position: relative;
        z-index: 10;
        box-shadow: 9px 16px 43px 0px rgba(0, 0, 0, 1), 9px -16px 43px 0px rgba(0, 0, 0, 0.75); 
        border-top: 1px solid rgba(#fff, 0.15);
        
        &::after{
            content: '';
            position: absolute;
            border-bottom: 1px solid rgba(#fff, 0.15);
            bottom: 0;
            left: 0;
            width: 100%;
            z-index: 10;
        }
        // text-align: lef

        @include min(740){
            text-align: center;
        }

        .svg-icon{
            margin-right: 20px;
        }
    }
    &__map-head-image{
        position: absolute;
        font-size: 20px;
        top: 50%;
        right: 40/1280*100%;
        margin-top: em(-131, 20);
        @include size(em(204, 20), em(256, 20));
        filter: drop-shadow(0 0 35px rgba(#07080b, 0.3));
        pointer-events: none;

         @include max(565) {
             font-size: 15px;
         }
         @include max(420) {
             font-size: 8px;
         }
    }
    &__map{
        height: 420px;
    }
    
    &__franchise{
        padding-bottom: 35px;
        padding-top: 40px;
        position: relative;
        box-shadow: 9px -16px 43px 0px rgba(0, 0, 0, 1); 
        background: transparent url(../img/images/bg-contacts-1.jpg) no-repeat top center / cover;
        &::before{
            content: '';
            position: absolute;
            border-bottom: 1px solid rgba(#fff, 0.15);
            top: 0;
            left: 0;
            width: 100%;
            z-index: 10;
        }
        // box-shadow: 0px 0px 38px 0px rgba(7, 8, 11, 0.51);

        // a{
        //         &:not([href*="mailto"]) {
        //         text-decoration: underline;
        //     }
        //     &:hover{
        //         color: $accent;
        //         text-decoration: none;
        //     }
        // }

        // .ttl-sm{
        //     margin-bottom: 17px;
        // }
    }
    &__franchise-row{
        max-width: 790px;
        margin: 0 auto;
        
        @include min(767){
            display: flex;
        }
    }
    &__franchise-logo{
        flex-shrink: 0;
        margin-right: 25px;
        text-align: center;
        @include max(766){
            margin-bottom: 15px;
        }
    }
    &__franchise-txt{
        p{
            font-size: 25px;
            font-weight: 300;
            line-height: 30px;
        }
    }
}

.contacts-info{
    font-size: 18px;
    max-width: 540px;
    margin: 0 auto 35px;

    &__ttl { 
        font-size: 20px;
        font-weight: 500;
        margin-bottom: 9px;

        img{
            vertical-align: middle;
            margin-right: 10px;
        }
    }

    .col{
        @include max(479){
            flex-basis: 100%;
        }
    }

    li{
        margin-bottom: 5px;
    }
    
    ul {
        padding-bottom: 7px;
    }
    a{
        &:hover{
            color: $accent;
            text-decoration: none;
        }
    }
    .icon{
        margin-right: 10px;
    }
}

 .contacts-soc-list {
     li{
         display: inline-block;
         margin-top: 2px;
         vertical-align: middle;
     }
     
     a{
         @include size(23px);
         background-color: #000000;
        text-align: center;
         color: #fff;
         display: flex;
         align-items: center;
         justify-content: center;

         &:hover{
             color: #fff;
             background-color:$accent;
         }
     }

     .svg-icon{
         @include size(13px);
     }

     &--type-1{

        

         li + li{
             margin-left: 7px;
         }
         a{
            @include size(auto);
            background: none;
            &:hover{
                background: none;
                transform: scale(0.90);
            }
         }
     }
 }